@import '../../styles/breakpoints';

.sb-navbar-toggle {
  --sb-icon__color: var(--sb-navbar-toggle-color, var(--sbt-white));
  align-items: center;
  color: var(--sb-navbar-toggle-color, var(--sbt-white));
  cursor: pointer;
  display: flex;
  height: var(--sbt-navbar-height);
  max-height: var(--sbt-navbar-height);
  overflow: hidden;

  &:hover {
    --sb-icon__hover-color: var(
      --sb-navbar-toggle-color-hover,
      var(--sbt-theme-tertiary-color-20)
    );
    color: var(
      --sb-navbar-toggle-color-hover,
      var(--sbt-theme-tertiary-color-20)
    );
  }
}

.sb-navbar-toggle__nested {
  --sb-navbar-toggle-color: var(--sbt-theme-tertiary-color-60);
  --sb-navbar-toggle-color-hover: var(--sbt-theme-tertiary-color-80);

  --sb-icon__color: var(--sbt-theme-tertiary-color-60);
  --sb-icon__hover-color: var(--sbt-theme-tertiary-color-80);
  align-items: baseline;
  height: inherit;

  @include window-width-max-small {
    padding: var(--sbt-spacing-20) 0;
  }
}

.sb-navbar-toggle__tooltip {
  padding: var(--sbt-spacing-40) var(--sbt-spacing-0);
}

.sb-navbar-toggle__inline-menu {
  @include window-width-max-small {
    flex-wrap: wrap;
    height: auto;
    max-height: none;
    padding-left: var(--sbt-spacing-50);
    width: 95vw;
  }
}

.sb-navbar-toggle__has-menu-item {
  @include window-width-max-small {
    padding-left: var(--sbt-spacing-0);

    .sb-navbar-toggle__mobile {
      margin-left: 19px;
    }
  }
}

.sb-navbar-toggle__mobile {
  width: 95%;

  @include window-width-max-small {
    border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
    border-top: 1px solid var(--sbt-theme-tertiary-color-20);
    padding: var(--sbt-spacing-20) 0;
  }
}
